import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { IImageModel } from '../../Models/image-model';
import { Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ImageService {

  private imageUrl = "assets/api/images/image.json";

  constructor(private http: HttpClient) { }

  getAllImages(): Observable<IImageModel[]> {
    return this.http.get<IImageModel[]>(this.imageUrl);
  }

  getImageById(imageId:string): Observable<IImageModel> {
    return this.getAllImages().pipe( map(images => images.find(image => image.ImageId == imageId )));
  }

  getCategories(): Promise<string[]> {
    return this.getAllImages().toPromise<IImageModel[]>()
      .then(images => images.map(image => image.Category));
  }

  getImagesByCategory(category: string): Promise<IImageModel[]> {
    if(category === 'all')
    {
      return this. getAllImages().toPromise();
    }
    return this. getAllImages().toPromise().then(images => images.filter(image => image.Category === category));
  }

  getImagesForCategory(): Observable<any> {
    const imagesForCategory = new Subject();
    this.getCategories().then(depts => {
      const departmentSet = new Set(depts);
      departmentSet.forEach(categ => {
        this.getAllImages().toPromise<IImageModel[]>()
          .then(images => images.filter(image => image.Category === categ))
          .then(images => imagesForCategory.next({ category: categ, images: images })
          );
      });
    });
    return imagesForCategory;
  }


  private handleError(err: HttpErrorResponse) {
    // in a real world app, we may send the server to some remote logging infrastructure
    // instead of just logging it to the console
    let errorMessage = '';
    if (err.error instanceof Error) {
      // A client-side or network error occurred. Handle it accordingly.
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return Observable.throw(errorMessage);
  }
}
