import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { FormBuilder, FormGroup, FormControl, Validators, NgForm } from '@angular/forms';
import { EmailService } from '../shared/services/email-service/email.service';

@Component({
  selector: 'app-request-qoute',
  templateUrl: './request-qoute.component.html',
  styleUrls: ['./request-qoute.component.css']
})
export class RequestQouteComponent implements OnInit {

  emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";
  // This object will hold the messages to be displayed to the user
  // Notice, each key in this object has the same name as the
  // corresponding form control
  formErrors = {
    'fullName': '',
    'emailAddress':'',
    'contactPhoneNumber':'',
    'message':''
  };

  // This object contains all the validation messages for this form
  validationMessages = {
    'fullName': {
      'required': 'Full name is required.',
      'minlength': 'Full name must be greater than 2 characters.',
      'maxlength': 'Full name must be less than 100 characters.'
    },
    'emailAddress': {
      'required': 'Email address is required.',
      'minlength': 'Email address address must be greater than 11 characters.',
      'maxlength': 'Email address address must be less than 150 characters.',
      'emailDomain': 'Email address domian should be devine-tech.com'
    },
    'contactPhoneNumber':{
      'required': 'Phone number is required.',
      'minlength': 'Phone number must be greater than 5 characters.',
      'maxlength': 'Phone number must be less than 150 characters.',
    },
    'message': {
      'required': 'Message is required.',
      'minlength': 'Message must be greater than 5 characters.',
      'maxlength': 'Message must be less than 150 characters.',
    }
  };

  constructor( private toastr: ToastrService, private fb: FormBuilder, emailService: EmailService) { }

  contactData: any;
  public RequestQouteForm: FormGroup;

  ngOnInit() {

    this.RequestQouteForm = this.fb.group({
      fullName: new FormControl('', [Validators.required, Validators.minLength(5), Validators.maxLength(60)]),
      emailAddress: new FormControl('', [Validators.required, Validators.minLength(11), Validators.maxLength(150)]),
      contactPhoneNumber: new FormControl('', [Validators.required, Validators.minLength(2), Validators.maxLength(60)]),
      message: new FormControl('', [Validators.required, Validators.maxLength(200)]),
    });

    this.RequestQouteForm.valueChanges.subscribe((data) =>{
      this.logValidationErrors(this.RequestQouteForm);
    });
  }

  public hasError = (controlName: string, errorName: string) =>{
    return this.RequestQouteForm.controls[controlName].hasError(errorName);
  }

  logValidationErrors(group: FormGroup = this.RequestQouteForm): void {
    Object.keys(group.controls).forEach((key: string) => {
      const abstractControl = group.get(key);
      this.formErrors[key] = '';
      if (abstractControl && !abstractControl.valid
        && (abstractControl.touched || abstractControl.dirty)) {
        const messages = this.validationMessages[key];
        for (const errorKey in abstractControl.errors) {
          if (errorKey) {
            this.formErrors[key] += messages[errorKey] + ' ';
          }
        }
      }  
      if (abstractControl instanceof FormGroup) {
        this.logValidationErrors(abstractControl);
      }
    });
  }


  OnSubmit(form: NgForm) {    
    // var formData = form.value;
    // const userData: User = this.getUserInformation(formData)
    // this.userService.registerUser(userData)
    //   .subscribe((data: any) => {
    //     if (data.succeeded == true) {
    //       this.resetForm(form);
    //       this.toastr.success('User registration successful', "Success!");
    //     }
    //     else
    //       this.toastr.error(data.errors[0].description);
    //   });
  }


}
