import { Component, OnInit } from '@angular/core';
import { ImageService } from '../shared/services/image-service/image.service';
import { ImageModel } from '../shared/Models/image-model';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {

  public ImageList: ImageModel[] = []; 
  public toolsImageList: ImageModel[] = []; 
  public projectImageList: ImageModel[] = []; 
  
  constructor(private imageService: ImageService) { }

  getAllImages() {
    return this.imageService. getAllImages().subscribe( images => {
      this.toolsImageList = images.filter(image => image.Category == 'tool');
      this.projectImageList = images.filter(image => image.Category == 'project');
    })
  };
  

  ngOnChanges() {
    this.getAllImages();
  }

  ngOnInit() {
    this.getAllImages();
  }

}
